@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI",
        "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas,
        "Courier New", monospace;
}

.timer-container {
    margin: 20px;
}
.timer-container h1 {
    text-align: center;
}

.controls {
}

.controls button {
    background-color: transparent;
    padding: 10px;
    margin: 15px;
    font-size: 20px;
    border: none;
    cursor: pointer;
}

.controls span {
    font-size: 40px;
    margin: 0 10px;
}

.control-icon {
    pointer-events: none;
}

.clock-container {
    background-color: transparent;
    margin: 20px;
    padding: 20px;
    border: solid 2px #000000;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    display: inline-block;
    width: 35vw;
}
.clock-container h1 {
    text-align: center;
    margin: 0;
}
.clock-container span {
    font-size: 60px;
}
.controls {
}

.controls button {
    background-color: transparent;
    padding: 10px;
    margin: 15px;
    font-size: 20px;
    border: none;
    cursor: pointer;
}

.controls span {
    font-size: 40px;
    margin: 0 10px;
}

.control-icon {
    pointer-events: none;
}

* {
    box-sizing: border-box;
    background-color: #70a1ff;
    font-family: "Noto Sans", sans-serif;
}
.App {
    text-align: center;
}

.Counters {
    display: flex;
    align-items: center;
    justify-content: center;
}

#break-label,
#session-label {
    margin: 0 20px;
}

